import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'
import countries from '../country'

export default {
    // title: 'Banks',
    title: {
        singular: 'Bank',
        plural: 'Banks',
    },
    icon: 'mdi-bank',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Bank Number',
                align: 'start',
                value: 'number',
                sortable: false
            },
            { text: 'Country', value: 'country', sortable: false, format: 'country', width: '150'  },
            { text: 'Currency', value: 'currency', sortable: false, component: () => import('./../components/items/fields/Currency.vue')  },
            // { text: 'USD Rate', value: 'usd_rate', sortable: false },
            // { text: 'SAR Rate', value: 'sar_rate', sortable: false },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels:[
                {
                    title: 'Currency',
                    fields:[
                        {
                            input: 'select',
                            name: 'currency',
                            bind: {
                                label: "Bank Currency",
                                prependInnerIcon: "mdi-currency-usd-circle-outline",
                                items: ['USD', 'EUR', 'SAR', 'TRY'],
                                required: true,
                                rules: [
                                    v => !!v || 'Currency is required',
                                ],
                            }
                        },
                    ]
                },
                {
                    title: 'Created By',
                    icon: "mdi-account",
                    fields:[
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.$store.state.auth.user.rights.filter(e => e.collection == 'bank')[0].write.allOwn == 'all'),
                            default: vm => vm.$store.state.auth.user._id,
                            bind: {
                                label: "User",
                                 
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                hideAddNew: true,
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Bank',
                    fields:[
                        {
                            input: 'text',
                            name: 'number',
                            bind: {
                                label: "Bank Number",
                                prependInnerIcon: 'mdi-card-account-details',
                                required: true,
                                rules: [
                                    v => !!v || 'ID is required',
                                ],
                            }
                        },

                        {
                            input: 'autocomplete',
                            name: 'country',
                            bind: {
                                label: "Bank Country",
                                prependInnerIcon: "mdi-flag",
                                items: countries,
                                itemText: "name",
                                itemValue: "code",
                                required: true,
                                hideAddNew: true,
                                rules: [
                                    v => !!v || 'Country is required',
                                ],
                            }
                        },
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'number',
                    bind: {
                        label: "Bank Number",
                        prependIcon: 'mdi-card-account-details',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                
                {
                    input: 'autocomplete',
                    name: 'country',
                    bind: {
                        label: "Country",
                        prependIcon: "mdi-flag",
                        items: countries,
                        itemText: "name",
                        itemValue: "code",
                        required: true,
                        hideAddNew: true,
                        rules: [
                            v => !!v || 'Country is required',
                        ],
                    }
                },
                {
                    input: 'select',
                    name: 'currency',
                    bind: {
                        label: "Bank Currency",
                        prependIcon: "mdi-currency-usd-circle-outline",
                        items: ['USD', 'EUR', 'SAR', 'TRY'],
                        required: true,
                        rules: [
                            v => !!v || 'Currency is required',
                        ],
                    }
                },
                // {
                //     input: 'text',
                //     name: 'usd_rate',
                //     default: () => 0.00,
                //     bind: {
                //         label: "USD Rate",
                //         type: 'number',
                //         prependIcon: "mdi-currency-usd",
                //         required: true,
                //         rules: [
                //             v => !!v || 'Currency is required',
                //         ],
                //     }
                // },
                // {
                //     input: 'text',
                //     name: 'sar_rate',
                //     default: () => 0.00,
                //     bind: {
                //         label: "SAR Rate",
                //         type: 'number',
                //         prependIcon: "mdi-currency-rial",
                       
                //         required: true,
                //         rules: [
                //             v => !!v || 'Currency is required',
                //         ],
                //     }
                // },
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'bank')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                        label: "User",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        hideAddNew: true,
                    }
                },
                
            ]
        },
        
    ]
}