
import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

import countries from '../country'
import store from './../store'
 
export default {
    // title: 'Customers',
    title: {
        singular: 'Customer',
        plural: 'Customers',
    },
    icon: 'mdi-account-box',
    get assign () {
        return ['admin', 'owner', 'manager'].indexOf(store.state.auth.user.role) !== -1
    } ,
    get select() {
        return ['admin', 'owner', 'manager'].indexOf(store.state.auth.user.role) !== -1
    },
    buttons: [
        {
            text: 'New Deposit',
            icon:'mdi-bank-transfer-in',
            bind: {
                dark:true,
                small: true,
                color: 'green',
            },
            on:(vm) => ({
                click () {
                    const { id } = vm.$route.params
                    vm.$router.replace({ path: '/deposit/new?customer=' + id, query: { backMode: true } })
                } 
            })
        }
    ],
    // import: true,
    // export: true,
    
    items: {
        buttons: tableButtons.get('new', 'delete','assign'),
        filters: [
            {
                input: 'text',
                name: 'tpc',
                bind: {
                    label: "TP Account",
                    prependInnerIcon: 'mdi-card-account-details',

                }
            },
            {
                input: 'text',
                name: 'name',
                bind: {
                    label: "Account Name",
                    prependInnerIcon: 'mdi-account-box',

                }
            },
            {
                input: 'autocomplete',
                name: 'country',
                bind: {
                    label: "Country",
                    prependInnerIcon: "mdi-flag",
                    items: countries,
                    itemText: "name",
                    itemValue: "code",
                    hideAddNew: true,

                }
            },
            {
                input: 'autocomplete',
                name: 'affiliate',
                bind: {
                    label: "Affiliate",
                    collection: 'affiliate',
                    itemText: "name",
                    itemValue: "_id",
                    prependInnerIcon: "mdi-account-network-outline",
                }
            },
            {
                input: 'autocomplete',
                name: 'user',
                if: state => (state.auth.user.rights.filter(e => e.collection == 'customer')[0].write.allOwn == 'all'),

                bind: {

                    label: "Owner",
                    prependInnerIcon: "mdi-account",
                    collection: 'user',
                    itemText: "displayName",
                    itemValue: "_id",
                    hideAddNew: true,
                    or: false,
                    query: {
                        role: ['!wakel', '!affiliate']
                    }
                }
            },
            {
                input: 'select',
                name: 'status',
                bind: {
                    label: "Status",
                    items: [
                        { text: 'All', value: null },
                        { text: 'New', value: 'new' },
                        { text: 'Call again', value: 'call again' },
                        { text: 'No Answer', value: 'no Answer' },
                        { text: 'Not interested', value: 'not interested' },
                    ],
                    itemText: "text",
                    itemValue: "value",
                    prependInnerIcon: "mdi-list-status",
                }
            }
        ],
        headers: [
            {
                text: 'TP Account',
                align: 'start',
                value: 'tpc',
                sortable: false
            },
            { text: 'Account name', value: 'name', sortable: false },
            { text: 'Country', value: 'country', format: 'country', width: '150'  },
            { text: 'Affiliate', value: 'affiliate.name', sortable: false },
            { text: 'Subaffiliate', value: 'subaffiliate.name', sortable: false },

            { text: 'FTD', value: 'ftd.amount', sortable: false, format: 'money' },
            { text: 'FTD Date', value: 'ftd.created_on', width: 120, sortable: false, format: 'date' },
            { 
                text: 'Owner Conversion', 
                value: 'ftd.user.displayName', 
                sortable: false,
                width: '200',
                component: () => import('./../components/items/fields/User.vue')
            },
            { text: 'profileImage', sortable: false, value: 'ftd.user.profileImage', hide: true },

            

            { text: 'Status', value: 'status', sortable: false },
            {
                text: 'Owner',
                sortable: false,
                value: 'user.displayName',
                width: '200',
                component: () => import('./../components/items/fields/User.vue')
            },
            { text: 'profileImage', sortable: false, value: 'user.profileImage', hide: true },

            // { text: 'Deposits', value: 'child.length' },
            // { text: 'Total Deposits', value: 'total[0].amount' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }

        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                {
                    title: "Status",
                    fields: [
                        {
                            input: 'select',
                            name: 'status',
                            default: () => 'new',
                            bind: {
                                label: "Status",
                                items: [
                                    { text: 'New', value: 'new' },
                                    { text: 'Call again', value: 'call again' },
                                    { text: 'No Answer', value: 'no Answer' },
                                    { text: 'Not interested', value: 'not interested' },
                                ],
                                itemText: "text",
                                itemValue: "value",
                                prependInnerIcon: "mdi-list-status",
                            }
                        }
                    ]
                },
                {
                    title: 'Country',
                    icon: "mdi-flag",
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'country',
                            bind: {
                                label: "Country",
                                // prependInnerIcon: "mdi-flag",
                                // template: {
                                //     item: () => import('../components/autocomplete/country/Item.vue'),
                                //     selection: () => import('../components/autocomplete/country/Selection.vue')
                                // },
                                template: 'country',
                                items: countries,
                                itemText: "name",
                                itemValue: "code",
                                required: true,
                                hideAddNew: true,
                                rules: [
                                    v => !!v || 'Country is required',
                                ],
                            }
                        }
                    ]
                },
                {
                    title: 'Owner',
                    icon: 'mdi-account',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.$store.state.auth.user.rights.filter(e => e.collection == 'deposit')[0].write.allOwn == 'all'),
                            default: vm => vm.$store.state.auth.user._id,
                            bind: () => ({
                                label: "Owner",
                                // prependInnerIcon: "mdi-account",
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                required: true,
                                rules: [
                                    v => !!v || 'Owner is required',
                                ],
                                or: false,
                                query: {
                                    role: ['!wakel', '!affiliate'],
                                },
                                hideAddNew: true,
                               
                            })
                        },
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: "Account",
                    fields: [
                        {
                            input: 'text',
                            name: 'tpc',
                            bind: {
                                label: "TP Account",
                                prependInnerIcon: 'mdi-card-account-details',
                                required: true,
                                rules: [
                                    v => !!v || 'ID is required',
                                ],
                            }
                        },
                        {
                            input: 'text',
                            name: 'name',
                            bind: {
                                label: "Account Name",
                                prependInnerIcon: 'mdi-account-box',
                                required: true,
                                rules: [
                                    v => !!v || 'ID is required',
                                ],
                            }
                        },
                    ]
                },
                {
                    title: 'Affiliate',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'affiliate',
                            bind: {
                                label: "Affiliate",
                                collection: 'affiliate',
                                itemText: "name",
                                itemValue: "_id",
                                prependInnerIcon: "mdi-account-network-outline",
                            },
                            on: (vm) => ({
                                input(evt) {
                                    const fieldName = vm.collectionName + '_' + 'subaffiliate'
                                    if (evt) {
                                        vm.props[fieldName] = {
                                            query: {
                                                affiliate: evt ? evt : '!',
                                            }
                                        }

                                    } else if (vm.props[fieldName]) {
                                        delete vm.props[fieldName]
                                    }
                                    
                                }
                            })
                        },
                        {
                            input: 'autocomplete',
                            name: 'subaffiliate',
                            if: (vm) => {
                                return vm.data.affiliate
                            },
                            default: null,
                            bind: (vm) => ({
                                label: "Subaffiliate",
                                collection: 'subaffiliate',
                                itemText: "name",
                                itemValue: "_id",
                                prependInnerIcon: "mdi-account-network-outline",
                                hideAddNew: true,
                                query: (() => { 
                                    return { affiliate: vm.data.affiliate ? vm.data.affiliate : '!' }
                                 })()
                            })
                        },
                    ]
                },
                {
                    title: 'Assignment History',
                    
                    fields: [
                        {
                            input: 'assignment',
                            if: (vm) => !vm.isNew,
                            name: 'history',
                            default: () => []
                        }
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'tpc',
                    bind: {
                        label: "TP Account",
                        prependIcon: 'mdi-card-account-details',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account Name",
                        prependIcon: 'mdi-account-box',
                        required: true,
                        rules: [
                            v => !!v || 'ID is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'country',
                    bind: {
                        label: "Country",
                        prependIcon: "mdi-flag",
                        items: countries,
                        itemText: "name",
                        itemValue: "code",
                        required: true,
                        hideAddNew: true,
                        rules: [
                            v => !!v || 'Country is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'affiliate',
                    bind: {
                        label: "Affiliate",
                        collection: 'affiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                    },
                    on: (vm) => ({
                        init (evt) {
                            if (vm.$refs.customer_subaffiliate) {
                                const [subaffiliate] = vm.$refs.customer_subaffiliate
                                vm.data['subaffiliate'] = null
                                subaffiliate.query.affiliate = evt[0] ? evt[0]._id : '!'
                            }
                        },
                        input (evt) {
                           
                            if (vm.$refs.customer_subaffiliate) {
                                const [subaffiliate] = vm.$refs.customer_subaffiliate
                                vm.data['subaffiliate'] = null 
                                subaffiliate.query.affiliate = evt ? evt : '!'
                            }
                        }
                    }) 
                },
                {
                    input: 'autocomplete',
                    name: 'subaffiliate',
                    default: null,
                    bind: {
                        label: "Subaffiliate",
                        collection: 'subaffiliate',
                        itemText: "name",
                        itemValue: "_id",
                        prependIcon: "mdi-account-network-outline",
                        hideAddNew: true,
                        query: { affiliate: '!' }
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'customer')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                       
                        label: "Owner",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        or: false,
                        query: {
                            role: ['!wakel', '!affiliate'],
                            // department: store.state.rtn
                        },
                        hideAddNew: true,
                    }
                },
                {
                    input: 'select',
                    name: 'status',
                    default: () => 'new',
                    bind: {
                        label: "Status",
                        items: [
                            { text: 'New', value: 'new' },
                            { text: 'Call again', value: 'call again' },
                            { text: 'No Answer', value: 'no Answer' },
                            { text: 'Not interested', value: 'not interested' },
                        ],
                        itemText: "text",
                        itemValue: "value",
                        prependIcon: "mdi-list-status",
                    }
                }
            ]
        },
        {
            label: 'Assignment History',
            if: (vm) => vm.$route.params.id,
            fields: [
                {
                    input: 'assignment',
                    name: 'history',
                    default: () => []
                } 
            ]
        }
    ]
}