import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
    // title: 'Payment Method',
    title: {
        singular: 'Payment Method',
        plural: 'Payment Methods',
    },
    icon: 'mdi-credit-card-settings-outline',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',

                value: 'name',
            },
            { text: 'Type', value: 'type', width: '20%' },
            { text: 'Active', value: 'active', sortable: false, width: '10%' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ],
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                {
                    title: 'Active',
                    fields: [
                        {
                            input: 'switch',
                            name: 'active',
                            default: true,
                            bind: {
                                label: "Active"
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Payment Method',
                    fields: [
                        {
                            input: 'text',
                            name: 'name',
                            col: {
                                md: 8
                            },
                            bind: {
                                label: "Name",
                                rules: [
                                    v => !!v || 'Name is required',
                                ],
                                required: true,
                            }
                        },
                        {
                            input: 'select',
                            name: 'type',
                            col: {
                                md: 4
                            },
                            bind: {
                                label: "Type",
                                rules: [
                                    v => !!v || 'Type is required',
                                ],
                                items: [
                                    {
                                        label: "Credit Card",
                                        value: "cc"
                                    },
                                    {
                                        label: "Wire",
                                        value: "wire"
                                    }
                                ],
                                itemText: "label",
                                itemValue: "value",
                                required: true,
                            }
                        },
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'select',
                    name: 'type',
                    bind: {
                        label: "Type",
                        rules: [
                            v => !!v || 'Type is required',
                        ],
                        items: [
                            {
                                label: "Credit Card",
                                value: "cc"
                            },
                            {
                                label: "Wire",
                                value: "wire"
                            }
                        ],
                        itemText: "label",
                        itemValue: "value",
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}