import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'

export default {
    // title: 'Affiliate',
    title: {
        singular: 'Affiliate',
        plural: 'Affiliates',
    },
    icon: 'mdi-account-network-outline',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',

                value: 'name',
            },
            { text: 'Active', value: 'active', sortable: false, width: '10%' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels: [
                {
                    title: 'Active',
                    fields: [
                        {
                            input: 'switch',
                            name: 'active',
                            default: true,
                            bind: {
                                label: "Active"
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Name',
                    fields: [
                        {
                            input: 'text',
                            name: 'name',
                            bind: {
                                label: "Name",
                                rules: [
                                    v => !!v || 'Name is required',
                                ],
                                required: true,
                            }
                        }
                    ]
                }
            ]
        }
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}